<template>
    <div class="demo">
        <ul>
            <li class="item">
                <el-table
                        ref="multipleTable"
                        :data="tableData3"
                        border
                        fit
                        tooltip-effect="dark"
                        style="width: 100%"
                        @selection-change="handleSelectionChange">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            label="日期"
                            width="120">
                        <template scope="scope">{{ scope.row.date }}</template>
                    </el-table-column>
                    <el-table-column
                            prop="name"
                            label="姓名"
                            width="120">
                    </el-table-column>
                    <el-table-column
                            label="地址"
                            show-overflow-tooltip>
                        <template scope="scope">
                            <el-input v-show="scope.row.edit" size="small" v-model="scope.row.address"></el-input>
                            <span v-show="!scope.row.edit">{{ scope.row.address }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="编辑">
                        <template scope="scope">
                            <el-button v-show='!scope.row.edit' type="primary" @click='scope.row.edit=true' size="small" icon="edit">编辑</el-button>
                            <el-button v-show='!scope.row.edit' type="primary" @click='getEdit(scope.row)' size="small" icon="edit">点击</el-button>
                            <el-button v-show='scope.row.edit' type="success" @click='scope.row.edit=false' size="small" icon="check">完成</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div style="margin-top: 20px">
                    <el-button @click="toggleSelection([tableData3[1], tableData3[2]])">切换第二、第三行的选中状态</el-button>
                    <el-button @click="toggleSelection()">取消选择</el-button>
                </div>
            </li>
        </ul>
    </div>
</template>
<style lang="scss">
    .demo {
        .item {
            margin: 15px 0;
        }
    }
</style>
<script type='text/ecmascript-6'>
    export default {
        name: 'table',
        data() {
            return {
                tableData3: null,
                multipleSelection: []
            }
        },
        created() {
            this.getList();
        },
        methods: {
            getList() {
                this.tableData3 = [{
                    date: '2016-05-03',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }, {
                    date: '2016-05-02',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }, {
                    date: '2016-05-04',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }, {
                    date: '2016-05-01',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }, {
                    date: '2016-05-08',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }, {
                    date: '2016-05-06',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }, {
                    date: '2016-05-07',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }].map(v => {
                    v.edit = false;
                    return v;
                });
            },
            getEdit(row) {
                console.log(row);
            },
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }
</script>



